'use strict';

const DesignRepository = require('./../repository/DesignRepository');

module.exports.beforeConfig = (config) => {
    DesignRepository.getFonts().done((fonts) => {
        config['font_names'] = fonts.map((font) => `${font.label}/${font.value}`).join(';');
    });
};



