/* eslint no-prototype-builtins: "off" */

'use strict';

const jQuery = require('jquery');

const mediator = require('./mediator');
const Request = require('./request');
const Response = require('./response');
let requestHandler;

let baseUrl = '';

/**
 * Request handler create a call to the api with several functions:
 *
 * That build a response and request object for retrieve data
 *
 * @module core/requestHandler
 * @requires jquery
 * @requires core/mediator
 * @requires core/request
 * @requires core/response
 * @author Florian Kroockmann <florian.kroockmann@lp-digital.fr>
 */
module.exports = requestHandler = {
  /**
   * Create a POST request
   *
   * @method create
   * @example RequestHandler.create('classcontent/Text/Paragraph/c9bf609ccdf0f3d5c63a542fd26632f3', data);
   * @param  {String} type
   * @param  {Object} data
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  create: function(type, data, criteria, orderBy, start, limit) {
    return handle('post', type, formatData(data, criteria, orderBy, start, limit));
  },

  /**
   * Create DELETE request
   *
   * @method del
   * @example RequestHandler.del('classcontent/Text/Paragraph/c9bf609ccdf0f3d5c63a542fd26632f3', data);
   * @param  {String} type
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  del: function(type, criteria, orderBy, start, limit) {
    return handle('delete', type, formatData({}, criteria, orderBy, start, limit));
  },

  /**
   * Create GET request
   *
   * @method read
   * @example RequestHandler.read('pages', {'title': 'preview'});
   * @param  {String} type
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  read: function(type, criteria, orderBy, start, limit) {
    return handle('get', type, formatData({}, criteria, orderBy, start, limit));
  },

  /**
   * Create PUT request
   *
   * @method update
   * @example RequestHandler.update('classcontent/Text/Paragraph/c9bf609ccdf0f3d5c63a542fd26632f3', data);
   * @param  {String} type
   * @param  {Object} data
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  update: function(type, data, criteria, orderBy, start, limit) {
    return handle('put', type, formatData(data, criteria, orderBy, start, limit));
  },

  /**
   * Create LINK request
   *
   * @method link
   * @example RequestHandler.link('classcontent/Text/Paragraph/c9bf609ccdf0f3d5c63a542fd26632f3', data);
   * @param  {String} type
   * @param  {Object} data
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  link: function(type, data, criteria, orderBy, start, limit) {
    return handle('link', type, formatData(data, criteria, orderBy, start, limit));
  },

  /**
   * Create PATCH request
   *
   * @method patch
   * @example RequestHandler.patch('classcontent/Text/Paragraph/c9bf609ccdf0f3d5c63a542fd26632f3', data);
   * @param  {String} type
   * @param  {Object} data
   * @param  {Object} criteria
   * @param  {Object} orderBy
   * @param  {Integer} start
   * @param  {Integer} limit
   * @return {Promise} data
   */
  patch: function(type, data, criteria, orderBy, start, limit) {
    return handle('patch', type, formatData(data, criteria, orderBy, start, limit));
  },

  /**
   * Set the base url of the api
   *
   * @method setBaseUrl
   * @example request.setBaseUrl('localhost');
   * @param {String} url
   * @return {requestHandler} this
   */
  setBaseUrl: function(url) {
    baseUrl = url;

    return this;
  },

  /**
   * Return the current base url
   *
   * @method getBaseUrl
   * @example request.getBaseUrl();
   * @return {String} base url
   */
  getBaseUrl: function() {
    return baseUrl;
  },

  /**
   * Send the request to the api
   *
   * @method send
   * @example request.send(request);
   * @param  {request} request
   * @return {Promise} response
   */
  send: function(request) {
    let dfd = jQuery.Deferred();
    let config = {};

    if (request instanceof Request) {
      mediator.publish('request:send:before', request);

      config = {
        url: encodeURI(request.getUrl()),
        type: request.getMethod(),
        data: request.getData(),
        headers: request.getHeaders(),
      };

      jQuery.ajax(config)
        .done((data, statusText, xhr) => {
          let response = buildResponse(xhr, statusText, data);

          mediator.publish('request:send:done', response);

          dfd.resolve(response.getData(), response, request);
        })
        .fail((xhr, statusText, errorThrown) => {
          let response = buildResponse(xhr, statusText, '', errorThrown);

          mediator.publish('request:send:fail', {
              request,
              response,
          });

          dfd.reject(response, request);
        });
    } else {
        throw new Error('The parameter must be an instance of Request');
    }

    return dfd.promise();
  },

  /**
   * Add search to the url
   *
   * @method addSearch
   * @example request.addSearch('/api', 'country', 'france')
   * @param {String} url
   * @param {String} key
   * @param {String} value
   * @return {String} url
   */
  addSearch: function(url, key, value) {
    return url + ((url.indexOf('?') !== -1) ? '&' : '?') + (key + '=' + value);
  },
};

/**
 * Build reponse from the xfr object
 *
 * @ignore
 * @param  {Object} xhr
 * @param  {String} statusText
 * @param  {Object} data
 * @param  {String} errorThrown
 * @return {response} response
 */
const buildResponse = function(xhr, statusText, data, errorThrown) {
  let response = new Response();

  buildHeaders(xhr.getAllResponseHeaders(), response);

  if (data) {
    response.setData(data);
  }

  response.setRawData(xhr.responseText);
  response.setStatus(xhr.status);
  response.setStatusText(statusText);

  if (errorThrown) {
    response.setErrorText(errorThrown);
  }

  return response;
};

/**
 * Build the data from request
 *
 * @ignore
 * @param  {Object} data
 * @param  {Object} criteria
 * @param  {Object} orderBy
 * @param  {Integer} start
 * @param  {Integer} limit
 * @return {Object} data
 */
const formatData = function(data, criteria, orderBy, start, limit) {
  return {
    data: data,
    criteria: criteria || null,
    orderBy: orderBy || {},
    start: start || 0,
    limit: limit || null,
  };
};

/**
 * Builds headers to the response
 *
 * @ignore
 * @param  {String} headers
 * @param  {response} response
 */
const buildHeaders = function(headers, response) {
  headers.split('\r').forEach((header) => {
    let identifierPos = header.indexOf(':');

    if (-1 !== identifierPos) {
      let name = header.substring(0, identifierPos).trim();
      let value = header.substring(identifierPos + 1).trim();

      response.addHeader(name, value);
    }
  });
};

/**
 * Handle the request
 *
 * @ignore
 * @param  {String} method
 * @param  {String} type
 * @param  {Object} data
 * @return {Promise} data
 */
const handle = function(method, type, data) {
  let url = baseUrl;
  let range;
  let request = new Request();

  request.headers = {};
  request.setContentType('application/json');
  request.addHeader('Accept', 'application/json');

  url = url + '/' + type;

  if ('get' === method) {
    request.setMethod('GET');
    url = computeCriteria(url, data);
  } else if ('put' === method || 'patch' === method || 'link' === method || 'delete' === method) {
    request.setMethod('update' === method ? 'put' : method);
    url = computeCriteria(url, data);

    if (data.hasOwnProperty('data')) {
      if ('patch' === method) {
        request.setData(computePatchOperations(data.data));
      } else {
        request.setData(data.data);
      }
    }
  } else if ('post' === method) {
    request.setMethod('POST');
    url = computeCriteria(url, data);

    if (data.hasOwnProperty('data')) {
      request.setData(data.data);
    }
  }

  if (data.hasOwnProperty('limit') && null !== data.limit) {
    range = (data.hasOwnProperty('start') ? data.start : '0') + ',' + data.limit;
    request.addHeader('Range', range);
  }

  url = computeOrderBy(url, data);

  request.setUrl(url);

  if (null !== request.getData()) {
    request.setData(JSON.stringify(request.getData()));
  }

  mediator.publish('rest:send:before', request);

  return requestHandler.send(request);
};

/**
 * Compute order by
 *
 * @ignore
 * @param  {String} url
 * @param  {Object} data
 * @return {String} url
 */
const computeOrderBy = function(url, data) {
  if (typeof data.orderBy === 'object') {
    for (let key in data.orderBy) {
      if (data.orderBy.hasOwnProperty(key)) {
        let order = data.orderBy[key];

        url = requestHandler.addSearch(url, 'order_by[' + key + ']', order);
      }
    }
  }

  return url;
};

/**
 * Compute criteria
 *
 * @ignore
 * @param  {String} url
 * @param  {Object} data
 * @return {String} url
 */
const computeCriteria = function(url, data) {
  let criteria = data.criteria || null;

  if (null === criteria) {
    return url;
  }

  for (let key in criteria) {
    if (criteria.hasOwnProperty(key)) {
      let criterion = criteria[key];

      if ('uid' === key || 'id' === key) {
        url = url + ((url[url.length - 1] === '/') ? '' : '/') + criterion.toString();
      } else {
        url = requestHandler.addSearch(url, key, criterion);
      }
    }
  }

  return url;
};

/**
 * Compute patch operations
 *
 * @ignore
 * @param  {Object} data
 * @return {Array} operations
 */
const computePatchOperations = function(data) {
  let operations = [];

  for (let key in data) {
    if (data.hasOwnProperty(key)) {
      let value = data[key];

      operations.push({
        op: 'replace',
        path: '/' + key,
        value: value,
      });
    }
  }

  return operations;
};



