'use strict';

const _ = require('lodash');
const jQuery = require('jquery');

const Container = require('./../../core/container');
const Zone = require('./../structure/zone');
const Translator = require('./../../core/translator');
const PopinManager = require('./../popin');
const Loader = require('./../../services/loader');

const itemTpl = require('./template/item.hbs');
const linkTpl = require('./template/link.hbs');

let config = require('./config');

const onItemClick = function(event) {
    let $current = jQuery(event.currentTarget);
    let popin = PopinManager.createPopin({
            'title': $current.find('.label').text(),
            'width': 630,
        });

    popin.setContent('<iframe width="560" height="315" src="' + $current.data('url') + '" frameborder="0" allowfullscreen></iframe>');

    popin.on('close', function() {
        popin.setContent('');
    });

    popin.display();
};

module.exports.onMenuClick = function() {
    let $list = jQuery('<ul class="help-list"></ul>');

    _.forEach(config.items, function(item) {
        let $item = jQuery(itemTpl(item));

        $item.on('click', onItemClick);

        $list.append($item);
    });


    Zone.setSmallPanel('Help', $list);
};

const getPopinHelp = function(url) {
    let popin = PopinManager.createPopin({
        'dialogClass': 'help-center ui-dialog ui-widget ui-widget-content ui-corner-all ui-front cloud-dialog ui-dialog-buttons ui-draggable ui-resizable',
        'width': 720,
        'buttons': [
            {
                class: 'popin popin-ok',
                text: Translator.translate('submit-help'),
                click: function() {
                    popin.hide();
                },
            },
        ],
    });
    let $iframe = jQuery(`<iframe style="border:0;" width="630" height="623" src="${url}"></iframe>`);

    Loader.start(popin.rootElement);
    $iframe.on('load', function() {
        Loader.stop(popin.rootElement);
    });

    popin.setContent($iframe);

    popin.on('open', function() {
        let popins = PopinManager.getAll();

        popins.forEach((currentPopin) => {
            if (popin !== currentPopin && currentPopin.invoke('isOpen')) {
                Container.set('prevent-hide-overlay', true);
            }
        });
    });

    popin.on('close', function() {
        setTimeout(function() {
            Container.set('prevent-hide-overlay', false);
        }, 100);
    });

    return popin;
};

module.exports.onGlobalMenuClick = function() {
    let locale = Translator.getLocale();
    let url = config.links.global[locale];
    let popin = getPopinHelp(url);

    Zone.hidePanels();

    popin.display();
};

module.exports.createLink = function(key) {
    if (Container.get('isMobile')) {
        return '';
    }

    let $link = jQuery(linkTpl({'key': key}));

    let locale = Translator.getLocale();
    let url = config.links[key];

    if (url && url[locale]) {
        url = url[locale];
    } else {
        url = config.links.global[locale];
    }

    $link.on('click', function() {
        let popin = getPopinHelp(url);

        Container.set('prevent-hide-overlay', true);
        Container.set('prevent-hide-toolbar', true);

        popin.on('close', function() {
            popin.setContent('');
            setTimeout(function() {
                Container.set('prevent-hide-overlay', false);
                Container.set('prevent-hide-toolbar', false);
            }, 100);
        });

        popin.display();
    });

    return $link;
};



