let $ = require('jquery');
window.Nestable = window.Nestable || {};
let Nestable = window.Nestable;
let hasTouch = 'ontouchstart' in document;

/**
 * Detect CSS pointer-events property
 * events are normally disabled on the dragging element to avoid conflicts
 * https://github.com/ausi/Feature-detection-technique-for-pointer-events/blob/master/modernizr-pointerevents.js
 */
let hasPointerEvents = (function() {
    let el = document.createElement('div');
    let docEl = document.documentElement;

    if (!('pointerEvents' in el.style)) {
        return false;
    }

    el.style.pointerEvents = 'auto';
    el.style.pointerEvents = 'x';
    docEl.appendChild(el);
    let supports = window.getComputedStyle && window.getComputedStyle(el, '').pointerEvents === 'auto';
    docEl.removeChild(el);
    return !!supports;
})();

let itemRenderer = function(itemAttrs, content, children, options, item) {
    let itemAttrsString = $.map(itemAttrs, function(value, key) {
        return ' ' + key + '="' + value + '"';
    }).join(' ');

    let html = '<' + options.itemNodeName + itemAttrsString + '>';
    html += '<' + options.handleNodeName + ' class="' + options.handleClass + '">';
    html += '<' + options.contentNodeName + ' class="' + options.contentClass + '">';
    html += content;
    html += '</' + options.contentNodeName + '>';
    html += '</' + options.handleNodeName + '>';
    html += children;
    html += '</' + options.itemNodeName + '>';

    return html;
};

let defaults = {
    contentCallback: function(item) {
        return item.content || '' ? item.content : item.id;
    },
    listNodeName: 'ol',
    itemNodeName: 'li',
    handleNodeName: 'div',
    contentNodeName: 'span',
    rootClass: 'dd',
    listClass: 'dd-list',
    itemClass: 'dd-item',
    dragClass: 'dd-dragel',
    handleClass: 'dd-handle',
    contentClass: 'dd-content',
    collapsedClass: 'dd-collapsed',
    placeClass: 'dd-placeholder',
    noDragClass: 'dd-nodrag',
    noChildrenClass: 'dd-nochildren',
    emptyClass: 'dd-empty',
    expandBtnHTML: '<button class="dd-expand" data-action="expand" type="button">Expand</button>',
    collapseBtnHTML: '<button class="dd-collapse" data-action="collapse" type="button">Collapse</button>',
    group: 0,
    maxDepth: 5,
    threshold: 20,
    fixedDepth: false, // fixed item's depth
    fixed: false,
    includeContent: false,
    scroll: false,
    scrollSensitivity: 1,
    scrollSpeed: 5,
    scrollTriggers: {
        top: 40,
        left: 40,
        right: -40,
        bottom: -40,
    },
    callback: function(l, e, p) {},
    onDragStart: function(l, e, p) {},
    beforeDragStop: function(l, e, p) {},
    listRenderer: function(children, options) {
        let html = '<' + options.listNodeName + ' class="' + options.listClass + '">';
        html += children;
        html += '</' + options.listNodeName + '>';

        return html;
    },
    itemRenderer: itemRenderer,
};

/**
* @param {element} element
* @param {object} options
**/
function Plugin(element, options) {
    this.w = $(document);
    this.el = $(element);
    options = options || defaults;

    if (options.rootClass !== undefined && options.rootClass !== 'dd') {
        options.listClass = options.listClass ? options.listClass : options.rootClass + '-list';
        options.itemClass = options.itemClass ? options.itemClass : options.rootClass + '-item';
        options.dragClass = options.dragClass ? options.dragClass : options.rootClass + '-dragel';
        options.handleClass = options.handleClass ? options.handleClass : options.rootClass + '-handle';
        options.collapsedClass = options.collapsedClass ? options.collapsedClass : options.rootClass + '-collapsed';
        options.placeClass = options.placeClass ? options.placeClass : options.rootClass + '-placeholder';
        options.noDragClass = options.noDragClass ? options.noDragClass : options.rootClass + '-nodrag';
        options.noChildrenClass = options.noChildrenClass ? options.noChildrenClass : options.rootClass + '-nochildren';
        options.emptyClass = options.emptyClass ? options.emptyClass : options.rootClass + '-empty';
    }

    this.options = $.extend({}, defaults, options);

    // build HTML from serialized JSON if passed
    if (this.options.json !== undefined) {
        this._build();
    }

    this.init();
}

Plugin.prototype = {

    init: function() {
        let list = this;

        list.reset();

        list.el.data('nestable-group', this.options.group);

        list.placeEl = $('<div class="' + list.options.placeClass + '"/>');

        let items = this.el.find(list.options.itemNodeName);
        $.each(items, function(k, el) {
            let item = $(el);
            let parent = item.parent();
            list.setParent(item);
            if (parent.hasClass(list.options.collapsedClass)) {
                list.collapseItem(parent.parent());
            }
        });

        // Append the .dd-empty div if the list don't have any items on init
        if (!items.length) {
            // this.appendEmptyElement(this.el);
        }

        list.el.on('click', 'button', function(e) {
            if (list.dragEl) {
                return;
            }

            let target = $(e.currentTarget);
            let action = target.data('action');
            let item = target.parents(list.options.itemNodeName).eq(0);

            if (action === 'collapse') {
                list.collapseItem(item);
            }
            if (action === 'expand') {
                list.expandItem(item);
            }
        });

        let onStartEvent = function(e) {
            let handle = $(e.target);
            if (!handle.hasClass(list.options.handleClass)) {
                if (handle.closest('.' + list.options.noDragClass).length) {
                    return;
                }
                handle = handle.closest('.' + list.options.handleClass);
            }
            if (!handle.length || list.dragEl) {
                return;
            }

            list.isTouch = /^touch/.test(e.type);
            if (list.isTouch && e.touches.length !== 1) {
                return;
            }

            e.preventDefault();
            list.dragStart(e.touches ? e.touches[0] : e);
        };

        let onMoveEvent = function(e) {
            if (list.dragEl) {
                e.preventDefault();
                list.dragMove(e.touches ? e.touches[0] : e);
            }
        };

        let onEndEvent = function(e) {
            if (list.dragEl) {
                e.preventDefault();
                list.dragStop(e.touches ? e.changedTouches[0] : e);
            }
        };

        if (hasTouch) {
            list.el[0].addEventListener('touchstart', onStartEvent, false);
            window.addEventListener('touchmove', onMoveEvent, false);
            window.addEventListener('touchend', onEndEvent, false);
            window.addEventListener('touchcancel', onEndEvent, false);
        }

        list.el.on('mousedown', onStartEvent);
        list.w.on('mousemove', onMoveEvent);
        list.w.on('mouseup', onEndEvent);

        let destroyNestable = function() {
            if (hasTouch) {
                list.el[0].removeEventListener('touchstart', onStartEvent, false);
                window.removeEventListener('touchmove', onMoveEvent, false);
                window.removeEventListener('touchend', onEndEvent, false);
                window.removeEventListener('touchcancel', onEndEvent, false);
            }

            list.el.off('mousedown', onStartEvent);
            list.w.off('mousemove', onMoveEvent);
            list.w.off('mouseup', onEndEvent);

            list.el.off('click');
            list.el.unbind('destroy-nestable');

            list.el.data('nestable', null);
        };

        list.el.bind('destroy-nestable', destroyNestable);
    },

    destroy: function() {
        this.el.trigger('destroy-nestable');
    },

    add: function(item) {
        let listClassSelector = '.' + this.options.listClass;
        let tree = $(this.el).children(listClassSelector);

        if (item.parent_id !== undefined) {
            tree = tree.find('[data-id="' + item.parent_id + '"]');
            delete item.parent_id;

            if (tree.children(listClassSelector).length === 0) {
                tree = tree.append(this.options.listRenderer('', this.options));
            }

            tree = tree.find(listClassSelector);
            this.setParent(tree.parent());
        }

        tree.append(this._buildItem(item, this.options));
    },

    replace: function(item) {
        let html = this._buildItem(item, this.options);

        this._getItemById(item.id)
            .replaceWith(html);
    },

    // use fade = 'fade' to fadeout item before removing.
    // by using time(string/msecs), you can control animation speed, default is jq 'slow'
    remove: function(itemId, fade, time) {
        let opts = this.options;
        let el = this.el;
        let item = this._getItemById(itemId);

        // animation time
        time = time || 'slow';

        /** removes item and additional elements from list
        * @param {element} item
        **/
        function removeItem(item) {
            // remove item
            item = item || this;
            item.remove();

            // remove empty children lists
            let emptyListsSelector = '.' + opts.listClass
                + ' .' + opts.listClass + ':not(:has(*))';
            $(el).find(emptyListsSelector).remove();

            // remove buttons if parents do not have children
            let buttonsSelector = '[data-action="expand"], [data-action="collapse"]';
            $(el).find(buttonsSelector).each(function() {
                let siblings = $(this).siblings('.' + opts.listClass);
                if (siblings.length === 0) {
                    $(this).remove();
                }
            });
        }

        // Setting fade to true, adds fadeOut effect to removing.
        if (fade === 'fade') {
            item.fadeOut(time, removeItem);
        } else {
            removeItem(item);
        }
    },

    _getItemById: function(itemId) {
        return $(this.el).children('.' + this.options.listClass)
            .find('[data-id="' + itemId + '"]');
    },

    _build: function() {
        let json = this.options.json;

        if (typeof json === 'string') {
            json = JSON.parse(json);
        }

        $(this.el).html(this._buildList(json, this.options));
    },

    _buildList: function(items, options) {
        if (!items) {
            return '';
        }

        let children = '';
        let that = this;

        $.each(items, function(index, sub) {
            children += that._buildItem(sub, options);
        });

        return options.listRenderer(children, options);
    },

    /**
    * @param {element} item
    * @param {array} options
    * @return {object} object with item values
    **/
    _buildItem: function(item, options) {
        /**
        * @param {object} text
        * @return {array} object values
        **/
        function escapeHtml(text) {
            let map = {
                '&': '&amp;',
                '<': '&lt;',
                '>': '&gt;',
                '"': '&quot;',
                "'": '&#039;',
            };

            return (text + '').replace(/[&<>"']/g, function(m) {
                return map[m];
            });
        }

        /**
        * @param {classe} classes
        * @return {classe} newClasse
        **/
        function filterClasses(classes) {
            let newClasses = {};

            for (let k in classes) {
                if (classes.hasOwnProperty(k)) {
                    newClasses[classes[k]] = classes[k];
                }
            }

            return newClasses;
        }

        /**
        * @param {element} item
        * @param {array} options
        * @return {object} object with item values
        **/
        function createClassesString(item, options) {
            let classes = item.classes || {};

            if (typeof classes === 'string') {
                classes = [classes];
            }

            let itemClasses = filterClasses(classes);
            itemClasses[options.itemClass] = options.itemClass;

            // create class string
            return $.map(itemClasses, function(val) {
                return val;
            }).join(' ');
        }

        /**
        * @param {attributes} attr
        * @return {object} all attributes for item
        **/
        function createDataAttrs(attr) {
            attr = $.extend({}, attr);

            delete attr.children;
            delete attr.classes;
            delete attr.content;

            let dataAttrs = {};

            $.each(attr, function(key, value) {
                if (typeof value === 'object') {
                    value = JSON.stringify(value);
                }

                dataAttrs['data-' + key] = escapeHtml(value);
            });

            return dataAttrs;
        }

        let itemAttrs = createDataAttrs(item);
        itemAttrs['class'] = createClassesString(item, options);

        let content = options.contentCallback(item);
        let children = this._buildList(item.children, options);
        let html = $(options.itemRenderer(itemAttrs, content, children, options, item));

        this.setParent(html);

        return html[0].outerHTML;
    },

    serialize: function() {
        let data;
        let list = this;
        let step = function(level) {
            let array = [];
            let items = level.children(list.options.itemNodeName);
            items.each(function() {
                let li = $(this);
                let item = $.extend({}, li.data());
                let sub = li.children(list.options.listNodeName);

                if (list.options.includeContent) {
                    let content = li.find('.' + list.options.contentClass).html();

                    if (content) {
                        item.content = content;
                    }
                }

                if (sub.length) {
                    item.children = step(sub);
                }
                array.push(item);
            });
            return array;
        };
        data = step(list.el.find(list.options.listNodeName).first());
        return data;
    },

    asNestedSet: function() {
        let list = this;
        let o = list.options;
        let depth = -1;
        let ret = [];
        let lft = 1;
        let items = list.el.find(o.listNodeName).first().children(o.itemNodeName);

        items.each(function() {
            lft = traverse(this, depth + 1, lft);
        });

        ret = ret.sort(function(a, b) {
            return (a.lft - b.lft);
        });

        return ret;

        /**
        * calculates the depth
        * @param {element} item
        * @param {number} depth
        * @param {number} lft
        * @return {number} lft
        **/
        function traverse(item, depth, lft) {
            let rgt = lft + 1;
            let id;
            let pid;

            if ($(item).children(o.listNodeName).children(o.itemNodeName).length > 0 ) {
                depth++;
                $(item).children(o.listNodeName).children(o.itemNodeName).each(function() {
                    rgt = traverse($(this), depth, rgt);
                });
                depth--;
            }

            id = parseInt($(item).attr('data-id'));
            pid = parseInt($(item).parent(o.listNodeName).parent(o.itemNodeName).attr('data-id')) || '';

            if (id) {
                ret.push({'id': id, 'parent_id': pid, 'depth': depth, 'lft': lft, 'rgt': rgt});
            }

            lft = rgt + 1;
            return lft;
        }
    },

    returnOptions: function() {
        return this.options;
    },

    serialise: function() {
        return this.serialize();
    },

    toHierarchy: function(options) {
        let o = $.extend({}, this.options, options);
        let ret = [];

        $(this.element).children(o.items).each(function() {
            let level = _recursiveItems(this);
            ret.push(level);
        });

        return ret;

        /**
        * @param {element} item
        * @return {element} currentItem
        **/
        function _recursiveItems(item) {
            let id = ($(item).attr(o.attribute || 'id') || '').match(o.expression || (/(.+)[-=_](.+)/));
            if (id) {
                let currentItem = {
                    'id': id[2],
                };
                if ($(item).children(o.listType).children(o.items).length > 0) {
                    currentItem.children = [];
                    $(item).children(o.listType).children(o.items).each(function() {
                        let level = _recursiveItems(this);
                        currentItem.children.push(level);
                    });
                }
                return currentItem;
            }
        }
    },

    toArray: function() {
        let o = $.extend({}, this.options, this);
        let sDepth = o.startDepthCount || 0;
        let ret = [];
        let left = 2;
        let list = this;
        let element = list.el.find(list.options.listNodeName).first();

        let items = element.children(list.options.itemNodeName);
        items.each(function() {
            left = _recursiveArray($(this), sDepth + 1, left);
        });

        ret = ret.sort(function(a, b) {
            return (a.left - b.left);
        });

        return ret;

        /**
        * calculates the depth
        * @param {element} item
        * @param {number} depth
        * @param {number} left
        * @return {number} left
        **/
        function _recursiveArray(item, depth, left) {
            let right = left + 1;
            let id;
            let pid;

            if (item.children(o.options.listNodeName).children(o.options.itemNodeName).length > 0) {
                depth++;
                item.children(o.options.listNodeName).children(o.options.itemNodeName).each(function() {
                    right = _recursiveArray($(this), depth, right);
                });
                depth--;
            }

            id = item.data().id;


            if (depth === sDepth + 1) {
                pid = o.rootID;
            } else {
                let parentItem = (item.parent(o.options.listNodeName)
                    .parent(o.options.itemNodeName)
                    .data());
                pid = parentItem.id;
            }

            if (id) {
                ret.push({
                    'id': id,
                    'parent_id': pid,
                    'depth': depth,
                    'left': left,
                    'right': right,
                });
            }

            left = right + 1;
            return left;
        }
    },

    reset: function() {
        this.mouse = {
            offsetX: 0,
            offsetY: 0,
            startX: 0,
            startY: 0,
            lastX: 0,
            lastY: 0,
            nowX: 0,
            nowY: 0,
            distX: 0,
            distY: 0,
            dirAx: 0,
            dirX: 0,
            dirY: 0,
            lastDirX: 0,
            lastDirY: 0,
            distAxX: 0,
            distAxY: 0,
        };
        this.isTouch = false;
        this.moving = false;
        this.dragEl = null;
        this.dragRootEl = null;
        this.dragDepth = 0;
        this.hasNewRoot = false;
        this.pointEl = null;
    },

    expandItem: function(li) {
        li.removeClass(this.options.collapsedClass);
    },

    collapseItem: function(li) {
        let lists = li.children(this.options.listNodeName);
        if (lists.length) {
            li.addClass(this.options.collapsedClass);
        }
    },

    expandAll: function() {
        let list = this;
        list.el.find(list.options.itemNodeName).each(function() {
            list.expandItem($(this));
        });
    },

    collapseAll: function() {
        let list = this;
        list.el.find(list.options.itemNodeName).each(function() {
            list.collapseItem($(this));
        });
    },

    setParent: function(li) {
        // Check if li is an element of itemNodeName type and has children
        if (li.is(this.options.itemNodeName) && li.children(this.options.listNodeName).length) {
            // make sure NOT showing two or more sets data-action buttons
            li.children('[data-action]').remove();
            li.prepend($(this.options.expandBtnHTML));
            li.prepend($(this.options.collapseBtnHTML));
        }
    },

    unsetParent: function(li) {
        li.removeClass(this.options.collapsedClass);
        li.children('[data-action]').remove();
        li.children(this.options.listNodeName).remove();
    },

    dragStart: function(e) {
        let mouse = this.mouse;
        let target = $(e.target);
        let dragItem = target.closest(this.options.itemNodeName);

        let position = {};
        position.top = e.pageY;
        position.left = e.pageX;

        let continueExecution = this.options.onDragStart.call(this, this.el, dragItem, position);

        if (typeof continueExecution !== 'undefined' && continueExecution === false) {
            return;
        }

        this.placeEl.css('height', dragItem.height());

        mouse.offsetX = e.pageX - dragItem.offset().left;
        mouse.offsetY = e.pageY - dragItem.offset().top;
        mouse.startX = mouse.lastX = e.pageX;
        mouse.startY = mouse.lastY = e.pageY;

        this.dragRootEl = this.el;
        this.dragEl = $(document.createElement(this.options.listNodeName)).addClass(this.options.listClass + ' ' + this.options.dragClass);
        this.dragEl.css('width', dragItem.outerWidth());

        this.setIndexOfItem(dragItem);

        // fix for zepto.js
        // dragItem.after(this.placeEl).detach().appendTo(this.dragEl);
        dragItem.after(this.placeEl);
        dragItem[0].parentNode.removeChild(dragItem[0]);
        dragItem.appendTo(this.dragEl);

        $(document.body).append(this.dragEl);
        this.dragEl.css({
            'left': e.pageX - mouse.offsetX,
            'top': e.pageY - mouse.offsetY,
        });
        // total depth of dragging item
        let i;
        let depth;
        let items = this.dragEl.find(this.options.itemNodeName);

        for (i = 0; i < items.length; i++) {
            depth = $(items[i]).parents(this.options.listNodeName).length;
            if (depth > this.dragDepth) {
                this.dragDepth = depth;
            }
        }
    },

    // Create sublevel.
    //  element : element which become parent
    //  item    : something to place into new sublevel
    createSubLevel: function(element, item) {
        let list = $('<' + this.options.listNodeName + '/>').addClass(this.options.listClass);
        if (item) list.append(item);
        element.append(list);
        this.setParent(element);
        return list;
    },

    setIndexOfItem: function(item, index) {
        index = index || [];

        index.unshift(item.index());

        if ($(item[0].parentNode)[0] !== this.dragRootEl[0]) {
            this.setIndexOfItem($(item[0].parentNode), index);
        } else {
            this.dragEl.data('indexOfItem', index);
        }
    },

    restoreItemAtIndex: function(dragElement, indexArray) {
        let currentEl = this.el;
        let lastIndex = indexArray.length - 1;

        /**
        * Put drag element at current element position.
        *
        * @param {element} currentEl
        * @param {element} dragElement
        **/
        function placeElement(currentEl, dragElement) {
            if (indexArray[lastIndex] === 0) {
                $(currentEl).prepend(dragElement.clone());
            } else {
                $(currentEl.children[indexArray[lastIndex] - 1]).after(dragElement.clone());
            }
        }
        // Diggin through indexArray to get home for dragElement.
        for (let i = 0; i < indexArray.length; i++) {
            if (lastIndex === parseInt(i)) {
                placeElement(currentEl, dragElement);
                return;
            }
            // element can have no indexes, so we have to use conditional here to avoid errors.
            // if element doesn't exist we defenetly need to add new list.
            let element = (currentEl[0]) ? currentEl[0] : currentEl;
            let nextEl = element.children[indexArray[i]];
            currentEl = (!nextEl) ? this.createSubLevel($(element)) : nextEl;
        }
    },

    dragStop: function(e) {
        // fix for zepto.js
        // this.placeEl.replaceWith(this.dragEl.children(this.options.itemNodeName + ':first').detach());
        let position = {
            'top': e.pageY,
            'left': e.pageX,
        };
        // Get indexArray of item at drag start.
        let srcIndex = this.dragEl.data('indexOfItem');

        let el = this.dragEl.children(this.options.itemNodeName).first();

        el[0].parentNode.removeChild(el[0]);

        this.dragEl.remove(); // Remove dragEl, cause it can affect on indexing in html collection.

        // Before drag stop callback
        let continueExecution = this.options.beforeDragStop.call(this, this.el, el, this.placeEl.parent());
        if (typeof continueExecution !== 'undefined' && continueExecution === false) {
            let parent = this.placeEl.parent();
            this.placeEl.remove();
            if (!parent.children().length) {
                this.unsetParent(parent.parent());
            }
            this.restoreItemAtIndex(el, srcIndex);
            this.reset();
            return;
        }

        this.placeEl.replaceWith(el);

        if (this.hasNewRoot) {
            if (this.options.fixed === true) {
                this.restoreItemAtIndex(el, srcIndex);
            } else {
                this.el.trigger('lostItem');
            }
            this.dragRootEl.trigger('gainedItem');
        } else {
            this.dragRootEl.trigger('change');
        }

        this.options.callback.call(this, this.dragRootEl, el, position);

        this.reset();
    },

    dragMove: function(e) {
        let list;
        let parent;
        let prev;
        let next;
        let depth;
        let opt = this.options;
        let mouse = this.mouse;

        this.dragEl.css({
            'left': e.pageX - mouse.offsetX,
            'top': e.pageY - mouse.offsetY,
        });

        // mouse position last events
        mouse.lastX = mouse.nowX;
        mouse.lastY = mouse.nowY;
        // mouse position this events
        mouse.nowX = e.pageX;
        mouse.nowY = e.pageY;
        // distance mouse moved between events
        mouse.distX = mouse.nowX - mouse.lastX;
        mouse.distY = mouse.nowY - mouse.lastY;
        // direction mouse was moving
        mouse.lastDirX = mouse.dirX;
        mouse.lastDirY = mouse.dirY;
        // direction mouse is now moving (on both axis)
        mouse.dirX = mouse.distX === 0 ? 0 : mouse.distX > 0 ? 1 : -1;
        mouse.dirY = mouse.distY === 0 ? 0 : mouse.distY > 0 ? 1 : -1;
        // axis mouse is now moving on
        let newAx = Math.abs(mouse.distX) > Math.abs(mouse.distY) ? 1 : 0;

        // do nothing on first move
        if (!mouse.moving) {
            mouse.dirAx = newAx;
            mouse.moving = true;
            return;
        }

        // do scrolling if enable
        let scrolled = false;

        if (opt.scroll) {
            if (typeof window.jQuery.fn.scrollParent !== 'undefined') {
                let scrollParent = this.el.scrollParent()[0];
                if (scrollParent !== document && scrollParent.tagName !== 'HTML') {
                    if ((opt.scrollTriggers.bottom + scrollParent.offsetHeight) - e.pageY < opt.scrollSensitivity)
                        scrollParent.scrollTop = scrolled = scrollParent.scrollTop + opt.scrollSpeed;
                    else if (e.pageY - opt.scrollTriggers.top < opt.scrollSensitivity)
                        scrollParent.scrollTop = scrolled = scrollParent.scrollTop - opt.scrollSpeed;

                    if ((opt.scrollTriggers.right + scrollParent.offsetWidth) - e.pageX < opt.scrollSensitivity)
                        scrollParent.scrollLeft = scrolled = scrollParent.scrollLeft + opt.scrollSpeed;
                    else if (e.pageX - opt.scrollTriggers.left < opt.scrollSensitivity)
                        scrollParent.scrollLeft = scrolled = scrollParent.scrollLeft - opt.scrollSpeed;
                } else {
                    if (e.pageY - $(document).scrollTop() < opt.scrollSensitivity)
                        scrolled = $(document).scrollTop($(document).scrollTop() - opt.scrollSpeed);
                    else if ($(window).height() - (e.pageY - $(document).scrollTop()) < opt.scrollSensitivity)
                        scrolled = $(document).scrollTop($(document).scrollTop() + opt.scrollSpeed);

                    if (e.pageX - $(document).scrollLeft() < opt.scrollSensitivity)
                        scrolled = $(document).scrollLeft($(document).scrollLeft() - opt.scrollSpeed);
                    else if ($(window).width() - (e.pageX - $(document).scrollLeft()) < opt.scrollSensitivity)
                        scrolled = $(document).scrollLeft($(document).scrollLeft() + opt.scrollSpeed);
                }
            }
        }

        if (this.scrollTimer) {
            clearTimeout(this.scrollTimer);
        }

        if (opt.scroll && scrolled) {
            this.scrollTimer = setTimeout(function() {
                $(window).trigger(e);
            }, 10);
        }

        // calc distance moved on this axis (and direction)
        if (mouse.dirAx !== newAx) {
            mouse.distAxX = 0;
            mouse.distAxY = 0;
        } else {
            mouse.distAxX += Math.abs(mouse.distX);
            if (mouse.dirX !== 0 && mouse.dirX !== mouse.lastDirX) {
                mouse.distAxX = 0;
            }
            mouse.distAxY += Math.abs(mouse.distY);
            if (mouse.dirY !== 0 && mouse.dirY !== mouse.lastDirY) {
                mouse.distAxY = 0;
            }
        }
        mouse.dirAx = newAx;

        /**
         * move horizontal
         */
        if (mouse.dirAx && mouse.distAxX >= opt.threshold) {
            // reset move distance on x-axis for new phase
            mouse.distAxX = 0;
            prev = this.placeEl.prev(opt.itemNodeName);
            // increase horizontal level if previous sibling exists, is not collapsed, and can have children
            if (mouse.distX > 0 && prev.length && !prev.hasClass(opt.collapsedClass) && !prev.hasClass(opt.noChildrenClass)) {
                // cannot increase level when item above is collapsed
                list = prev.find(opt.listNodeName).last();
                // check if depth limit has reached
                depth = this.placeEl.parents(opt.listNodeName).length;
                if (depth + this.dragDepth <= opt.maxDepth) {
                    // create new sub-level if one doesn't exist
                    if (!list.length) {
                        this.createSubLevel(prev, this.placeEl);
                    } else {
                        // else append to next level up
                        list = prev.children(opt.listNodeName).last();
                        list.append(this.placeEl);
                    }
                }
            }
            // decrease horizontal level
            if (mouse.distX < 0) {
                // we can't decrease a level if an item preceeds the current one
                next = this.placeEl.next(opt.itemNodeName);
                if (!next.length) {
                    parent = this.placeEl.parent();
                    this.placeEl.closest(opt.itemNodeName).after(this.placeEl);
                    if (!parent.children().length) {
                        this.unsetParent(parent.parent());
                    }
                }
            }
        }

        let isEmpty = false;

        // find list item under cursor
        if (!hasPointerEvents) {
            this.dragEl[0].style.visibility = 'hidden';
        }
        this.pointEl = $(document.elementFromPoint(e.pageX - document.body.scrollLeft, e.pageY - (window.pageYOffset || document.documentElement.scrollTop)));
        if (!hasPointerEvents) {
            this.dragEl[0].style.visibility = 'visible';
        }
        if (this.pointEl.hasClass(opt.handleClass)) {
            this.pointEl = this.pointEl.closest(opt.itemNodeName);
        }
        if (this.pointEl.hasClass(opt.emptyClass)) {
            isEmpty = true;
        } else if (!this.pointEl.length || !this.pointEl.hasClass(opt.itemClass)) {
            return;
        }

        // find parent list of item under cursor
        let pointElRoot = this.pointEl.closest('.' + opt.rootClass);
        let isNewRoot = this.dragRootEl.data('nestable-id') !== pointElRoot.data('nestable-id');

        /**
         * move vertical
         */
        if (!mouse.dirAx || isNewRoot || isEmpty) {
            // check if groups match if dragging over new root
            if (isNewRoot && opt.group !== pointElRoot.data('nestable-group')) {
                return;
            }

            // fixed item's depth, use for some list has specific type, eg:'Volume, Section, Chapter ...'
            if (this.options.fixedDepth && this.dragDepth + 1 !== this.pointEl.parents(opt.listNodeName).length) {
                return;
            }

            // check depth limit
            depth = this.dragDepth - 1 + this.pointEl.parents(opt.listNodeName).length;
            if (depth > opt.maxDepth) {
                return;
            }
            let before = e.pageY < (this.pointEl.offset().top + this.pointEl.height() / 2);
            parent = this.placeEl.parent();
            // if empty create new list to replace empty placeholder
            if (isEmpty) {
                list = $(document.createElement(opt.listNodeName)).addClass(opt.listClass);
                list.append(this.placeEl);
                this.pointEl.replaceWith(list);
            } else if (before) {
                this.pointEl.before(this.placeEl);
            } else {
                this.pointEl.after(this.placeEl);
            }

            if (!parent.children().length) {
                this.unsetParent(parent.parent());
            }
            if (!this.dragRootEl.find(opt.itemNodeName).length) {
                this.appendEmptyElement(this.dragRootEl);
            }
            // parent root list has changed
            this.dragRootEl = pointElRoot;
            if (isNewRoot) {
                this.hasNewRoot = this.el[0] !== this.dragRootEl[0];
            }
        }
    },
    // Append the .dd-empty div to the list so it can be populated and styled
    appendEmptyElement: function(element) {
        // element.append('<div class="' + this.options.emptyClass + '"/>');
    },
};

$.fn.nestable = function(params) {
    let lists = this;
    let retval = this;
    let args = arguments;

    if (!('Nestable' in window)) {
        window.Nestable = {};
        Nestable.counter = 0;
    }

    lists.each(function() {
        let plugin = $(this).data('nestable');

        if (!plugin) {
            Nestable.counter++;
            $(this).data('nestable', new Plugin(this, params));
            $(this).data('nestable-id', Nestable.counter);
        } else {
            if (typeof params === 'string' && typeof plugin[params] === 'function') {
                if (args.length > 1) {
                    let pluginArgs = [];
                    for (let i = 1; i < args.length; i++) {
                        pluginArgs.push(args[i]);
                    }
                    retval = plugin[params](...pluginArgs);
                } else {
                    retval = plugin[params]();
                }
            }
        }
    });

    return retval || lists;
};

module.exports.itemRenderer = itemRenderer;



