'use strict';

const _ = require('lodash');
const jQuery = require('jquery');
const imageLoader = require('blueimp-load-image');

const Mediator = require('./../../../core/mediator');

const ContentManager = require('./../component/ContentManager');
const SaveManager = require('./../component/SaveManager');

const AddContent = require('./addContent');
const Loader = require('./../../../services/loader');
const ImageCrop = require('./../service/imageCrop');
const Uploader = require('./../service/uploader');
const PluginUpload = require('./../plugin/upload');
const ImagePlaceholder = require('./../service/imagePlaceholder');
const TextPlaceholder = require('./../service/textPlaceholder');

const getImageContent = (content) => {
    let imageContent = content.getProperties('is_image') ? content : null;
    if (!imageContent) {
        let $childrenImage = content.$.find('.bb-content.media-image');

        if ($childrenImage.length) {
            imageContent = ContentManager.getContentByNode($childrenImage);
        }
    }

    return imageContent;
};

module.exports.afterDrop = (content) => {
    let imageContent = getImageContent(content);

    if (imageContent) {
        ImagePlaceholder.apply(imageContent);
    }
};

module.exports.afterSubmitParameters = (content) => {
    let imageContent = getImageContent(content);

    if (imageContent) {
        TextPlaceholder.globalApply();
        ImagePlaceholder.apply(imageContent);
        ImageCrop.checkCrop();
        ImageCrop.refresh(imageContent);
    }
};

module.exports.afterAdd = (content) => {
    let imageContent = getImageContent(content);

    if (imageContent) {
        ImagePlaceholder.apply(imageContent);
        AddContent.empty(imageContent);
    }
};

module.exports.beforeMobileAdd = (content) => {
    if (content && content.definition.properties.is_image) {
        AddContent.mobile.before.hidePanels(content);
    }
};

module.exports.afterMobileAdd = (content) => {
    let imageContent = getImageContent(content);

    if (imageContent) {
        ImagePlaceholder.apply(content);
        AddContent.mobile.after.showMenu(imageContent);
    }
};

module.exports.afterMobileDrop = (content) => {
    let imageContent = getImageContent(content);

    if (imageContent) {
        ImagePlaceholder.apply(imageContent);
    }
};

module.exports.initGlobalDndEvent = () => {
    jQuery('body').on('dragenter', '.bb-content.media-image', function(event) {
        let content = ContentManager.getContentByNode(jQuery(event.currentTarget));

        if (-1 === _.indexOf(content.getForbiddenPlugins(), 'upload')) {
            event.preventDefault();
        }
    });

    jQuery('body').on('dragover', '.bb-content.media-image', function(event) {
        let content = ContentManager.getContentByNode(jQuery(event.currentTarget));

        if (-1 === _.indexOf(content.getForbiddenPlugins(), 'upload')) {
            event.preventDefault();
            jQuery(this).addClass('cloud-placeholder-droppable');
        }
    });

    jQuery('body').on('dragleave', '.bb-content.media-image', function(event) {
        let content = ContentManager.getContentByNode(jQuery(event.currentTarget));

        if (-1 === _.indexOf(content.getForbiddenPlugins(), 'upload')) {
            jQuery('.cloud-placeholder-droppable').removeClass('cloud-placeholder-droppable');
        }
    });


    jQuery('body').on('drop', '.bb-content.media-image', function(event) {
        let content = ContentManager.getContentByNode(jQuery(event.currentTarget));

        if (-1 === _.indexOf(content.getForbiddenPlugins(), 'upload')) {
            event.preventDefault();

            let file = event.originalEvent.dataTransfer.files[0];

            Loader.start(content.$);
            Uploader.upload(file, {content}).done(function(response) {
                PluginUpload.updateFileOfMedia(content, response).done(() => {
                    SaveManager.save().done(() => {
                        content.refresh().done(() => {
                            Mediator.publish('after:upload', content);
                            Loader.stop(content.$);
                        });
                    });
                });
            });
        }
    });
};

module.exports.beforeUpload = (data) => {
    let {file, config} = data;

    if (!(config.content && config.content.getProperties('is_image')) || file.type === 'image/gif') {
        return;
    }

    let dfd = jQuery.Deferred();
    let imageConfig = {
        'quality': 80,
        'options': {
            'maxWidth': 1920,
            'canvas': true,
        },
    };

    imageLoader.parseMetaData(file, (data) => {
        if (data.exif) {
            imageConfig.options.orientation = data.exif.get('Orientation');
        }

        imageLoader(file, function(cvs) {
            dfd.resolve(cvs.toDataURL(file.type, imageConfig.quality / 100));
        }, imageConfig.options);
    });

    data.promise = dfd.promise();
};


module.exports.afterUpload = (content) => {
    let $figure = content.$;
    let $img = content.$.find('img');

    $img.on('load', function() {
        setTimeout(function() {
            let left = ($figure.width() - $img.width()) / 2 / $figure.width() * 100;
            let positionCenter;
            let ratio = $img.height() / $figure.outerWidth() * 100;

            $img.attr('style', 'left: ' + left + '%');
            positionCenter = {'top': 0, 'left': left};
            $figure.attr('style', 'padding-top: ' + ratio + '%');

            content.addParameter('position', positionCenter);
            content.addParameter('ratio', ratio + '');
            content.addParameter('focus', {'top': 50, 'left': 50});

            SaveManager.save().done(() => {
                content.refresh();
            });
        }, 0);
    });
};



