'use strict';

const jQuery = require('jquery');

const Translator = require('./../../../../core/translator');
const Zone = require('./../../../../services/structure/zone');
const Formbuilder = require('./../../../../services/form');
const Loader = require('./../../../../services/loader');

const PageRepository = require('./../../repository/page');
const LangRepository = require('./../../../user/repository/LangRepository');

const TableTpl = require('./template/table.hbs');
const ItemTpl = require('./template/item.hbs');
const PaginationTpl = require('./template/pagination.hbs');
const PaginationItemTpl = require('./template/pagination_item.hbs');

let $table;
let searchData = {};
// let sortArray = [];
let langActivated = false;
let categoryActivated = false;

const ITEM_PER_PAGE = 50;
const NB_PREV_PAGE = 5;
const NB_NEXT_PAGE = 4;

module.exports.init = () => {
    initSmallPanel();
};

const initSmallPanel = () => {
    let $container = jQuery('<div />');

    Loader.start($container);

    computeSearch({});

    LangRepository.findAll().done((langs) => {
        PageRepository.getTypes().done((pageTypes) => {
            PageRepository.getCategories().done((pageCategories) => {
                langActivated = !!langs.find((lang) => {
                    return lang.is_active === true;
                });
                categoryActivated = pageCategories.length > 0;
                let formConfig = {
                    'elements': {
                        'title': {
                            type: 'text',
                            placeholder: Translator.translate('search_page_title'),
                            value: '',
                            group: {
                                id: 'title',
                                title: Translator.translate('search_page_title'),
                            },
                        },
                        'has_draft_only': {
                            type: 'switch',
                            label: Translator.translate('show_only_no_saved_pages'),
                            labelIcon: '<i class="icon is-drafted icon-circle ml-1 brand-color"></i>',
                            value: false,
                            group: {
                                id: 'title',
                            },
                        },
                        'lang': {
                            type: 'select',
                            options: function() {
                                let options = {'all': Translator.translate('all_langs')};

                                langs.forEach((lang) => {
                                    return options[lang.id] = Translator.translate(`new_page_${lang.id}`);
                                });

                                return options;
                            }(),
                            value: 'all',
                            group: {
                                id: 'filter',
                                title: Translator.translate('filters'),
                            },
                        },
                        'is_online': {
                            type: 'select',
                            options: {
                                'all': Translator.translate('all_status'),
                                '1': Translator.translate('online'),
                                '0': Translator.translate('offline'),
                            },
                            value: 'all',
                            group: {
                                id: 'filter',
                                title: Translator.translate('filters'),
                            },
                        },
                        'type': {
                            type: 'select',
                            options: function() {
                                let options = {'all': Translator.translate('all_page_types')};

                                pageTypes.forEach((pageType) => {
                                    let type = Translator.translate(`page_type_${pageType['unique_name']}`) === `page_type_${pageType['unique_name']}` ? `new_page_${pageType['unique_name']}` : `page_type_${pageType['unique_name']}`;

                                    return options[pageType['unique_name']] = type;
                                });

                                return options;
                            }(),
                            value: 'all',
                            group: {
                                id: 'filter',
                            },
                        },
                        'category': {
                            type: 'select',
                            options: function() {
                                let options = {'all': Translator.translate('all_page_categories')};

                                pageCategories.forEach((pageCategory) => {
                                    return options[pageCategory] = Translator.translate(`page_category_${pageCategory}`);
                                });

                                return options;
                            }(),
                            value: 'all',
                            group: {
                                id: 'filter',
                            },
                        },
                        'tags': {
                            type: 'selectTag',
                            placeholder: Translator.translate('filter_by_tags'),
                            value: '',
                            group: {
                                id: 'filter',
                            },
                        },
                    },
                    'form': {
                        'submit_wrap_class': 'd-block',
                        'submit_class': 'btn btn-primary btn-block',
                        'submit_label': Translator.translate('search'),
                    },
                };

                if (!langActivated) {
                    delete formConfig.elements.lang;
                }

                if (!categoryActivated) {
                    delete formConfig.elements.category;
                }

                let form = Formbuilder.getForm(formConfig);

                form.on('submit', (data) => {
                    computeSearch(data);
                    doSearch();
                });

                const addCleanFiltersBtn = () => {
                    let $cleanFilters = jQuery(`<a class="setting-link" href="#">${Translator.translate('clean_filters')}</a>`);

                    $cleanFilters.on('click', function(e) {
                        e.preventDefault();
                        form.reset();

                        addCleanFiltersBtn();
                    });

                    $container.find('.element_title').prepend($cleanFilters);
                };

                Loader.stop($container);

                $container.append(form.render());

                addCleanFiltersBtn();

                initFullPanel();
            });
        });
    });

    Zone.setSmallPanel(Translator.translate('search_page'), $container);
};

const cleanSearch = () => {
    searchData = {};
    // sortArray = [];
};

const computeSearch = (data) => {
    cleanSearch();
    for (let key in data) {
        if (data.hasOwnProperty(key)) {
            let value = data[key];

            if (value && value !== 'all') {
                if (key === 'has_draft_only') {
                    searchData[key] = '1';
                } else if (key === 'tags') {
                    searchData[key] = value.map((tag) => tag.label).join(',');
                } else {
                    searchData[key] = value;
                }
            }
        }
    }
};

const cleanTable = () => {
    $table.find('tr:not(.tr-head)').remove();
    $table.parent().find('.cloud-nav').remove();
    $table.parent().find('.count-page').remove();
};

const insertItem = (item) => {
    item.statusLabel = Translator.translate(item.is_online ? 'online' : 'offline');
    item.pageTypeLabel = Translator.translate(Translator.translate(`page_type_${item.type}`) === `page_type_${item.type}` ? `new_page_${item.type}` : `page_type_${item.type}`);
    item.pageCategoryLabel = categoryActivated ? Translator.translate(item.category ? `page_category_${item.category}` : 'none') : false;
    item.langLabel = langActivated ? item.lang : false;
    item.modifiedLabel = new Date(item.modified).toLocaleDateString();
    item.publishedLabel = item.published_at ? new Date(item.published_at).toLocaleDateString() : '';
    item.createdLabel = item.created_at ? new Date(item.created_at).toLocaleDateString() : '';

    let $item = jQuery(ItemTpl(item));

    $item.on('click', function() {
        window.location.href = item.url;
    });

    $item.insertAfter($table.find('tr:last'));
};

const getContentRangeData = (contentRange) => {
    let data = /^(.*)-(.*)\/(.*)/.exec(contentRange);

    return {start: parseInt(data[1], 10), end: parseInt(data[2], 10), total: parseInt(data[3], 10)};
};

const doSearch = () => {
    Loader.start($table);
    PageRepository.get(searchData).done((pages, response) => {
        $table[pages.length ? 'removeClass' : 'addClass']('d-none');

        $table.siblings('.no-result').remove();

        cleanTable();

        if (!pages.length) {
            jQuery(`<p class="no-result">${Translator.translate('search_page_no_result')}</p>`).insertBefore($table);
        } else {
            generatePagination(response);
            generateCount(response);
        }

        Loader.stop($table);

        pages.forEach((page) => {
            insertItem(page);
        });
    });
};

const roundUpMax = (value) => {
    return (~~((value + 49) / 50) * 50);
};

const generatePagination = (response) => {
    let rangeData = getContentRangeData(response.headers['content-range']);
    let $pagination = jQuery(PaginationTpl({
        hasFirst: rangeData.start > 0,
        hasLast: (rangeData.end + 1) < rangeData.total,
    }));
    let nbPrevPages = 0;

    if (rangeData.end + 1 === rangeData.total) {
        return;
    }

    const createPage = (start, insert) => {
        let $page = jQuery(PaginationItemTpl({
            range: `${start}-${start + ITEM_PER_PAGE - 1}`,
            numPage: start === 0 ? 1 : (start / ITEM_PER_PAGE + 1),
            active: start === rangeData.start,
        }));

        $page.find('a').on('click', function() {
            searchData.range = jQuery(this).data('range');

            doSearch();
        });

        if (insert === 'after') {
            $page.insertAfter($pagination.find('.prev-btn'));
        } else {
            $page.insertBefore($pagination.find('.next-btn'));
        }
    };

    // Calcul previous page
    let minPrev = rangeData.start - (NB_PREV_PAGE * ITEM_PER_PAGE) >= 0 ? rangeData.start - (5 * ITEM_PER_PAGE) : 0;
    for (let i = rangeData.start - ITEM_PER_PAGE; i >= minPrev; i = i - ITEM_PER_PAGE) {
        nbPrevPages = nbPrevPages + 1;
        createPage(i, 'after');
    }

    // Calcul current page
    createPage(rangeData.start);

    // Calcul next page
    let maxPages = 0;
    let nbNextPages = (roundUpMax(rangeData.total) - (rangeData.end + 1)) / ITEM_PER_PAGE;
    if (nbPrevPages + 1 + nbNextPages > NB_NEXT_PAGE + NB_PREV_PAGE + 1) {
        nbNextPages = (nbPrevPages + 1 + nbNextPages) - (NB_NEXT_PAGE + NB_PREV_PAGE + 1);
        maxPages = nbNextPages;
    }

    const maxNext = roundUpMax(rangeData.total) - (maxPages * 50);
    for (let i = roundUpMax(rangeData.end); i < maxNext; i = i + ITEM_PER_PAGE) {
        createPage(i);
    }

    $pagination.find('a.prev').on('click', function(e) {
        e.preventDefault();
        searchData['range'] = `${rangeData.start - ITEM_PER_PAGE}-${rangeData.start - 1}`;
        doSearch();
    });

    $pagination.find('a.next').on('click', function(e) {
        e.preventDefault();
        searchData['range'] = `${rangeData.end + 1}-${ITEM_PER_PAGE + rangeData.end}`;
        doSearch();
    });

    $pagination.insertBefore($table);
};

const generateCount = (response) => {
    let rangeData = getContentRangeData(response.headers['content-range']);
    let $content = jQuery(`<p class="count-page mb-0">${Translator.translate('page_count', {nb: rangeData.total})}</p>`);

    $content.insertBefore($table);
};

